module EdelweissData.Base.Types

//  This file is used from Fable projects and functions/types used here
// have to be transpile-able to JS. If you make modifications here, please
// check that the SAFE Client apps with Fable still compile. Thanks!

open EdelweissData.Base

type PrimitiveDataType =
    | TypeString
    | TypeUri
    | TypeInt
    | TypeDouble
    | TypeBool
    | TypeDatasetId
    | TypeSmiles
    | TypeImage
    | TypeJson

    override this.ToString() =
        match this with
        | TypeString -> "String"
        | TypeUri -> "URI"
        | TypeInt -> "Integer"
        | TypeDouble -> "Double"
        | TypeBool -> "Boolean"
        | TypeDatasetId -> "Dataset ID"
        | TypeSmiles -> "SMILES"
        | TypeImage -> "Image"
        | TypeJson -> "JSON"

type DataType =
    | Primitive of PrimitiveDataType
    | TypeArray of PrimitiveDataType

    override this.ToString() =
        match this with
        | Primitive t -> t.ToString()
        | TypeArray t -> sprintf "%s array" (t.ToString())

type IndexType =
    | FullTextSearchIndex
    | TermsAggregationIndex
    | SubstructureSearchIndex
    | ChemicalSimilaritySearchIndex

type NumericStatistics<'a> =
    { Mean : double
      StandardDeviation : double
      Min : 'a
      Max : 'a }

type AggregationBucket =
    { DocCount : int
      TermName : Identifiers.TermName }

type Statistics =
    | NoStatistics
    | IntStatistics of NumericStatistics<int64>
    | DoubleStatistics of NumericStatistics<double>
    | TermsStatistics of AggregationBucket array

type ColumnDataType =
    | PrimitiveColumn of PrimitiveDataType
    | ArrayColumn of valueSeparator : string * arrayType : PrimitiveDataType

    static member ToDataType = function
        | PrimitiveColumn primitiveDataType -> Primitive primitiveDataType
        | ArrayColumn (_, primitiveDataType) -> TypeArray primitiveDataType

    static member ArrayValueSeparator = function
        | PrimitiveColumn _ -> None
        | ArrayColumn (separator, _) -> Some separator

type Column =
    { // Name must be unique within one schema.
      Name : string
      Description : string
      ColumnDataType : ColumnDataType
      // List of strings used to indicate missing values in the CSV that are treated as null (e.g. "N/A")
      MissingValueIdentifiers : string list
      Indices : Set<IndexType>
      Visible : bool
      // The RDF predicate that links the data row to values in this column.
      // If none is given, one will be generated based on this column's name.
      RdfPredicate : string option
      Statistics : Statistics
    }

    member this.DataType = ColumnDataType.ToDataType this.ColumnDataType

type Schema =
    { Columns : Column array }

    static member Validate schema =
        let duplicateColumnNames =
            schema.Columns
            |> Array.map (fun column -> column.Name)
            |> Array.countBy id
            |> Array.filter (fun (name, count) -> count > 1)
            |> Array.map (fun (name, count) -> name)
            |> Array.toList

        if duplicateColumnNames.IsEmpty then
            Ok ()
        else
            Error (Errors.DuplicateColumnNames duplicateColumnNames)
