let InProgressDatasetCopyFrom : Format<Guid -> Guid -> string -> string, unit, string, string, Guid * Guid * string> =
    "/datasets/%O/in-progress/copy-from/%O/versions/%s"

let createRouteInProgressDatasetCopyFrom (baseUrl : Url) (targetId : InProgressDatasetId)
    (sourceId : PublishedDatasetIdAndVersionOrLatest) =
    let relativeUrl =
        sprintf InProgressDatasetCopyFrom (targetId.Unwrap()) (sourceId.Id.Unwrap())
            (sourceId.VersionOrLatest.ToString())
    UrlHelpers.join(baseUrl, relativeUrl)

let InProgressDatasetGetSample : Format<Guid -> string, unit, string, string, Guid> =
    "/datasets/%O/in-progress/sample"

let createRouteInProgressDatasetGetSample (baseUrl : Url) (dataset : InProgressDatasetId) =
    let relativeUrl = sprintf InProgressDatasetGetSample (dataset.Unwrap())
    UrlHelpers.join(baseUrl, relativeUrl)

