let DatasetPermissions : Format<Guid -> string, unit, string, string, Guid> = "/datasets/%O/permissions"

let DatasetAddUser : Format<Guid -> string, unit, string, string, Guid> = "/datasets/%O/permissions/users/add"

let DatasetAddGroup : Format<Guid -> string, unit, string, string, Guid> = "/datasets/%O/permissions/groups/add"

let DatasetRemoveUser : Format<Guid -> string, unit, string, string, Guid> = "/datasets/%O/permissions/users/delete"

let DatasetRemoveGroup : Format<Guid -> string, unit, string, string, Guid> = "/datasets/%O/permissions/groups/delete"

let DatasetChangeVisibility : Format<Guid -> string, unit, string, string, Guid> = "/datasets/%O/permissions/visibility"

let private createRouteDataset (urlFormat: Format<Guid -> string, unit, string, string, Guid>) (baseUrl : Url) (datasetId : Identifiers.DatasetId) =
    let relativeUrl = sprintf urlFormat (datasetId.Unwrap())
    UrlHelpers.join(baseUrl, relativeUrl)

let createRouteDatasetPermissions = createRouteDataset DatasetPermissions

let createRouteDatasetAddUser = createRouteDataset DatasetAddUser

let createRouteDatasetAddGroup = createRouteDataset DatasetAddGroup

let createRouteDatasetRemoveUser = createRouteDataset DatasetRemoveUser

let createRouteDatasetRemoveGroup = createRouteDataset DatasetRemoveGroup

let createRouteDatasetVisibility = createRouteDataset DatasetChangeVisibility

