module EdelweissData.Base.Values

//  This file is used from Fable projects and functions/types used here
// have to be transpile-able to JS. If you make modifications here, please
// check that the SAFE Client apps with Fable still compile. Thanks!

open FsToolkit.ErrorHandling

type Smiles =
    {
        Original: string
        Canonical: string option
        Similarity : float option
    }
    static member Default (original : string) : Smiles =
        {
            Original = original
            Canonical = None
            Similarity = None
        }

type PrimitiveValue =
    | ValueNull
    | ValueString of string
    | ValueUri of string
    | ValueInt of int64
    | ValueDouble of double
    | ValueBool of bool
    | ValueDatasetId of System.Guid
    | ValueSmiles of Smiles
    | ValueImage of string
    | ValueJson of Utils.JsonValue

exception private ImplosionException

type PrimitiveValueArray =
    | ValueEmptyArray
    | ValueStringArray of string []
    | ValueUriArray of string []
    | ValueIntArray of int64 []
    | ValueDoubleArray of double []
    | ValueBoolArray of bool []
    | ValueDatasetIdArray of System.Guid []
    | ValueSmilesArray of Smiles []
    | ValueImageArray of string []
    | ValueJsonArray of Utils.JsonValue []

    static member Explode = function
        | ValueEmptyArray -> [||]
        | ValueStringArray values -> Array.map ValueString values
        | ValueUriArray values -> Array.map ValueUri values
        | ValueIntArray values -> Array.map ValueInt values
        | ValueDoubleArray values -> Array.map ValueDouble values
        | ValueBoolArray values -> Array.map ValueBool values
        | ValueDatasetIdArray values -> Array.map ValueDatasetId values
        | ValueSmilesArray values -> Array.map ValueSmiles values
        | ValueImageArray values -> Array.map ValueImage values
        | ValueJsonArray values -> Array.map ValueJson values

    static member Implode values =
        result {
            if Array.isEmpty values then
                return ValueEmptyArray
            else
                try
                    match values.[0] with
                    | ValueString _ ->
                        return ValueStringArray (Array.map (function ValueString x -> x | _ -> raise ImplosionException) values)
                    | ValueUri _ ->
                        return ValueUriArray (Array.map (function ValueUri x -> x | _ -> raise ImplosionException) values)
                    | ValueInt _ ->
                        return ValueIntArray (Array.map (function ValueInt x -> x | _ -> raise ImplosionException) values)
                    | ValueDouble _ ->
                        return ValueDoubleArray (Array.map (function ValueDouble x -> x | _ -> raise ImplosionException) values)
                    | ValueBool _ ->
                        return ValueBoolArray (Array.map (function ValueBool x -> x | _ -> raise ImplosionException) values)
                    | ValueDatasetId _ ->
                        return ValueDatasetIdArray (Array.map (function ValueDatasetId x -> x | _ -> raise ImplosionException) values)
                    | ValueSmiles _ ->
                        return ValueSmilesArray (Array.map (function ValueSmiles x -> x | _ -> raise ImplosionException) values)
                    | ValueImage _ ->
                        return ValueImageArray (Array.map (function ValueImage x -> x | _ -> raise ImplosionException) values)
                    | ValueJson _ ->
                        return ValueJsonArray (Array.map (function ValueJson x -> x | _ -> raise ImplosionException) values)
                    | ValueNull _ ->
                        return! Error (Errors.ArrayImplosionError "Null value in an array")
                with
                    ImplosionException -> return! Error (Errors.ArrayImplosionError "Incompatible values")

        }


type Value =
    | ValuePrimitive of PrimitiveValue
    | ValueArray of PrimitiveValueArray

    static member OfArray (values : PrimitiveValue []) : Result<Value, Errors.Error> =
        result {
            let! valueArray = PrimitiveValueArray.Implode values
            return ValueArray valueArray
        }
