module EdelweissData.Thoth.Utils

//  Make sure that the code below is the same in EdelweissData.Thoth.Net and EdelweissData.Thoth.Fable

open EdelweissData.Base
open FsToolkit.ErrorHandling
open Thoth.Json
open Fable.Core
open Fable.Core.JsInterop

[<Emit("parseInt($0)")>]
let private parseInt (_: string) : int32 = jsNative

let encodeInt (n : int64) =
    n.ToString() |> parseInt |> Encode.int

[<Emit("isFinite($0) && Math.floor($0) === $0")>]
let isIntegralValue (_: JsonValue) : bool = jsNative

let int64OnlyDecoder : string -> JsonValue -> Result<int64, (string * ErrorReason)>  =
    fun (path : string) (token : JsonValue) ->
        if isIntegralValue token then
            Ok (unbox token |> int64)
        else
            Error (path, BadPrimitive("an int64", token))

let toStringCustom spaces (fromDomain : 'domain -> 'transfer) (encoder : 'transfer -> JsonValue) (domain : 'domain) : string =
    let transfer = fromDomain domain
    let json = encoder transfer
    Encode.toString spaces json

let toString<'domain, 'transfer> : (('domain -> 'transfer) -> ('transfer -> JsonValue) -> ('domain) -> string) = toStringCustom 0

let toStringPretty<'domain, 'transfer> : (('domain -> 'transfer) -> ('transfer -> JsonValue) -> ('domain) -> string) = toStringCustom 2

let fromStringResult (toDomain : 'transfer -> Result<'domain, Errors.Error>) (decoder : Decoder<'transfer>) (str : string) : Result<'domain, Errors.Error> =
    result {
        let! decodedJson =
            Decode.fromString decoder str
            |> Result.mapError (fun err -> Errors.ThothDecodingError (err.ToString()))
        let! domain = toDomain decodedJson
        return domain
    }

let fromString (toDomain : 'transfer -> 'domain) (decoder : Decoder<'transfer>) (str : string) : Result<'domain, Errors.Error> =
    fromStringResult (toDomain >> Result.Ok) decoder str

let (private decodeJsonValue : Decoder<Utils.JsonValue>), (private decodeJsonObject : Decoder<Utils.JsonObject>) =
    let primitiveDecoder =
        Decode.oneOf [ Decode.nil Utils.ValueNull
                       int64OnlyDecoder |> Decode.map Utils.ValueInt
                       Decode.float |> Decode.map Utils.ValueDouble
                       Decode.bool |> Decode.map Utils.ValueBool
                       Decode.string |> Decode.map Utils.ValueString ]
    // We have to be a bit careful here because we have a recursive decoder and
    // will get a stack overflow if things aren't sufficiently lazily defined
    let rec arrayDecoder path value =
        Decode.list decoder
        |> Decode.map Utils.ValueArray
        |> fun d -> d path value
    and valueObjectDecoder path value =
        Decode.dict decoder
        |> Decode.map Utils.ValueObject
        |> fun d -> d path value
    and objectDecoder path value =
        Decode.dict decoder
        |> fun d -> d path value
    and decoder path value =
        Decode.oneOf [primitiveDecoder; arrayDecoder; valueObjectDecoder] path value
    decoder, objectDecoder

let rec private encodeJsonValue : Encoder<Utils.JsonValue> =
    function
    | Utils.ValueNull -> Encode.nil
    | Utils.ValueString str -> Encode.string str
    | Utils.ValueInt n -> encodeInt n
    | Utils.ValueDouble x -> Encode.float x
    | Utils.ValueBool b -> Encode.bool b
    | Utils.ValueObject fields -> encodeJsonObject fields
    | Utils.ValueArray values ->
        values
        |> List.map encodeJsonValue
        |> Encode.list
and private encodeJsonObject : Encoder<Utils.JsonObject> =
    fun fields ->
    fields
    |> Map.map (fun _ value -> encodeJsonValue value)
    |> Map.toList
    |> Encode.object

module JsonValue =

    let encode = encodeJsonValue
    let decode = decodeJsonValue
    let toString = toString id encode
    let fromString = fromString id decode

module JsonObject =

    let encode = encodeJsonObject
    let decode = decodeJsonObject
    let toString = toString id encode
    let fromString = fromString id decode
