module EdelweissData.Base.Transfer.Values

//  This file is used from Fable projects and functions/types used here
// have to be transpile-able to JS. If you make modifications here, please
// check that the SAFE Client apps with Fable still compile. Thanks!

open EdelweissData.Base
open FsToolkit.ErrorHandling

let private toUntypedDomainPrimitiveValue = function
    | Utils.ValueNull -> Values.ValueNull
    | Utils.ValueString str -> Values.ValueString str
    | Utils.ValueInt x -> Values.ValueInt x
    | Utils.ValueDouble x -> Values.ValueDouble x
    | Utils.ValueBool b -> Values.ValueBool b
    | (Utils.ValueObject _ | Utils.ValueArray _) as jsonValue -> Values.ValueJson jsonValue

let private toDomainPrimitiveValue primitiveDataType value =
    result {
        match primitiveDataType, value with
        | _, Utils.ValueNull -> return Values.ValueNull
        | Types.TypeJson, _ -> return Values.ValueJson value
        | Types.TypeString, Utils.ValueString str -> return Values.ValueString str
        | Types.TypeUri, Utils.ValueString str -> return Values.ValueUri str
        | Types.TypeDatasetId, Utils.ValueString str ->
            let! guid = Utils.parseGuid str
            return (Values.ValueDatasetId guid)
        | Types.TypeInt, Utils.ValueInt x -> return Values.ValueInt x
        | Types.TypeDouble, Utils.ValueDouble x -> return Values.ValueDouble x
        | Types.TypeDouble, Utils.ValueInt x -> return Values.ValueDouble (double x)
        | Types.TypeBool, Utils.ValueBool b -> return Values.ValueBool b
        | Types.TypeSmiles, Utils.ValueObject fields ->
            match Map.tryFind "original" fields |> Option.defaultValue Utils.ValueNull with
            | Utils.ValueString original ->
                let! canonical =
                    match Map.tryFind "canonical" fields |> Option.defaultValue Utils.ValueNull with
                    | Utils.ValueString canonical -> Ok (Some canonical)
                    | Utils.ValueNull -> Ok None
                    | value -> Utils.decodingError "Canonical smiles should be a string but is %A." value
                let! similarity =
                    match Map.tryFind "similarity" fields |> Option.defaultValue Utils.ValueNull with
                    | Utils.ValueDouble similarity -> Ok (Some similarity)
                    | Utils.ValueInt similarity -> Ok (Some (double similarity))
                    | Utils.ValueNull -> Ok None
                    | value -> Utils.decodingError "Smiles similarity should be a double but is %A." value
                return Values.ValueSmiles { Original = original; Canonical = canonical; Similarity = similarity }
            | Utils.ValueNull -> return Values.ValueNull
            | value -> return! Utils.decodingError "Original smiles should be a string but is %A." value
        | Types.TypeImage, Utils.ValueString uri ->
            return (Values.ValueImage uri)
        | dataType, value -> return! Utils.decodingError "Cannot decode value %A at type %A." value dataType
    }

let private toUntypedDomainValue value =
    result {
        match value with
        | Utils.ValueNull | Utils.ValueString _ | Utils.ValueInt _ | Utils.ValueDouble _ | Utils.ValueBool _ | Utils.ValueObject _ ->
            let value' = toUntypedDomainPrimitiveValue value
            return Values.ValuePrimitive value'
        | Utils.ValueArray values ->
            let values' = List.map toUntypedDomainPrimitiveValue values
            return! Values.Value.OfArray (Array.ofList values')
    }

let private toDomainValue dataType value =
    result {
        match dataType with
            | Types.Primitive primitiveDataType ->
                let! primitiveValue = toDomainPrimitiveValue primitiveDataType value
                return Values.ValuePrimitive primitiveValue
            | Types.TypeArray primitiveDataType ->
                match value with
                | Utils.ValueArray values ->
                    let! values' = List.traverseResultM (toDomainPrimitiveValue primitiveDataType) values
                    return! Values.Value.OfArray (Array.ofList values')
                | _ ->
                    return! Utils.decodingError "Expected an array of values, but got a single one."
    }

let private fromDomainPrimitiveValue = function
    | Values.ValueNull -> Utils.ValueNull
    | Values.ValueString str -> Utils.ValueString str
    | Values.ValueUri uri -> Utils.ValueString uri
    | Values.ValueInt x -> Utils.ValueInt x
    | Values.ValueDouble x -> Utils.ValueDouble x
    | Values.ValueBool b -> Utils.ValueBool b
    | Values.ValueDatasetId id -> Utils.ValueString (id.ToString())
    | Values.ValueSmiles smiles ->
        Utils.ValueObject <| Map.ofList [
            yield ("original", Utils.ValueString smiles.Original)
            if Option.isSome smiles.Canonical then yield ("canonical", Utils.ValueString smiles.Canonical.Value)
            if Option.isSome smiles.Similarity then yield ("similarity", Utils.ValueDouble smiles.Similarity.Value)
        ]
    | Values.ValueImage uri -> Utils.ValueString uri
    | Values.ValueJson jsonValue -> jsonValue

let private fromDomain = function
    | Values.ValuePrimitive value -> fromDomainPrimitiveValue value
    | Values.ValueArray values ->
        let values' = Array.map fromDomainPrimitiveValue (Values.PrimitiveValueArray.Explode values)
        Utils.ValueArray (List.ofArray values')

type Value =
    | JsonValue of Utils.JsonValue

    static member ToUntypedDomainPrimitiveValue (JsonValue value) =
        toUntypedDomainPrimitiveValue value
    static member ToDomainPrimitiveValue primitiveDataType (JsonValue value) =
        toDomainPrimitiveValue primitiveDataType value
    static member ToUntypedDomainValue (JsonValue value) =
        toUntypedDomainValue value
    static member ToDomainValue dataType (JsonValue value) =
        toDomainValue dataType value
    static member FromDomainPrimitiveValue value =
        JsonValue (fromDomainPrimitiveValue value)
    static member FromDomain value =
        JsonValue (fromDomain value)

let (|ValueNull|_|) (arg : Value) =
    match arg with
    | JsonValue Utils.ValueNull -> Some ()
    | _ -> None

let (|ValueString|_|) (arg : Value) =
    match arg with
    | JsonValue (Utils.ValueString x) -> Some x
    | _ -> None

let (|ValueInt|_|) (arg : Value) =
    match arg with
    | JsonValue (Utils.ValueInt x) -> Some x
    | _ -> None

let (|ValueDouble|_|) (arg : Value) =
    match arg with
    | JsonValue (Utils.ValueDouble x) -> Some x
    | _ -> None

let (|ValueBool|_|) (arg : Value) =
    match arg with
    | JsonValue (Utils.ValueBool x) -> Some x
    | _ -> None

let (|ValueObject|_|) (arg : Value) =
    match arg with
    | JsonValue (Utils.ValueObject x) -> Some x
    | _ -> None

let (|ValueArray|_|) (arg : Value) =
    match arg with
    | JsonValue (Utils.ValueArray x) -> Some x
    | _ -> None

let valueNull = JsonValue Utils.ValueNull
let valueString x = JsonValue (Utils.ValueString x)
let valueInt x = JsonValue (Utils.ValueInt x)
let valueDouble x = JsonValue (Utils.ValueDouble x)
let valueBool x = JsonValue (Utils.ValueBool x)
let valueObject x = JsonValue (Utils.ValueObject x)
let valueArray x = JsonValue (Utils.ValueArray x)